import React from "react"
import { connect } from "react-redux"
import { Table } from "react-bootstrap"

class CustomerTop extends React.Component {
    state = {
        info: []
    }
    numberWithCommas(x) {
        return (x).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
    static getDerivedStateFromProps = (props, state) => {
        let result = props.customer.filter(obj => {
            return obj.year === parseInt(props.customer_year)
        })
        state.info = result[0]
        return state
    }

    total = (months, count = false) => {
        let sum = 0

        if (count === true) {
            sum = months.reduce((accumulator, object) => {
                return accumulator + object.count;
            }, 0);
        }
        else {
            sum = months.reduce((accumulator, object) => {
                return accumulator + object.sum;
            }, 0);
        }

        return sum
    }

    render() {

        return (
            <div className="customer-content__header">
                <h5 className="customer-content__header__name">{this.props.name}</h5>
                <div className="customer-info">
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                                <th>Total Sales</th>
                                <th>Total Amount</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.info === undefined ?
                                <tr>
                                    <td>0</td>
                                    <td>0</td>
                                </tr> :
                                <tr>
                                    <td>{this.total(this.state.info.months, true)}</td>
                                    <td>{this.numberWithCommas(this.total(this.state.info.months))}</td>
                                </tr>}

                        </tbody>
                    </Table>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {

    };
};

const mapDispatchToProps = {

};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CustomerTop);
